<template>
    <div class="container">
        <p class="title">我的订单</p>
        <van-list class="outer"
                  v-model="loading"
                  :finished="finished"
                  @load="onLoad"
        >
            <div
                    class="item"
                    v-for="item in orders"
                    :key="item.couponId"
                    :title="item"
            >
                <div class="item-left">
                    <div class="time">{{item.created_at}}</div>
                    <div class="latency">服务{{item.minute}}分钟</div>
                </div>
                <div class="item-right">
                    <div class="status">{{item.refund===0?'成功': (item.refund===3 ?'完成':'申请中') }}</div>
                    <div class="price">￥{{item.fee}}</div>
                </div>
            </div>
        </van-list>


    </div>
</template>

<script>
    import {List} from 'vant';
    import {Toast} from 'vant';

    import {GET_ORDER, APPEND_ORDER, DRIVER_VERIFY, FETCH_OPENID} from '../../common/config'
    import {get, post} from '../../common/http'
    import store from '../../storage'

    export default {
        components: {
            [List.name]: List
        },

        data() {
            return {
                loading: false,
                finished: true,
                orders: [],
                data: {},
                driverId: 0,
                page: 0

            };
        },

        computed: {},

        methods: {
            initOrder() {
                this.page = 1;
                const params = {openId: this.data.openid}
                post(GET_ORDER, params, res => {
                    this.orders = res.data
                    this.driverId = res.driverId
                    this.finished = false
                })
            },
            onLoad() {
                this.page++
                const params = {driverId: this.driverId, page: this.page}
                post(APPEND_ORDER, params, res => {
                    window.console.log('on load', res.data)
                    const size = res.data.length
                    if (0 === size) {
                        this.finished = true
                    } else {
                        for (let val of res.data) {
                            this.orders.push(val)
                        }
                    }
                    this.loading = false;
                })
            }
        },
        created() {
            const data = store.fetch()
            if (data && data.hasOwnProperty("openid")) {
                // has open id?
                this.data = data
                // if (data.hasOwnProperty("id")) {
                //     // init order.
                //     this.initOrder()
                // } else {
                const params = {openId: data.openid}
                // is bind?
                post(DRIVER_VERIFY, params, res => {
                        let resData = res;
                        resData.openid = data.openid
                        store.save(resData)
                        this.data = resData
                        this.initOrder()
                    }, () => this.$router.push({name: 'bind', query: {from: 'order'}})
                );
                // }
            } else {
                const code = store.urlParam('code')
                if (code) {
                    const params = {code}
                    get(FETCH_OPENID, params, res => {
                        this.data.openid = res.openId;
                        store.save(this.data)
                        let param = {openId: this.data.openid}
                        post(DRIVER_VERIFY, param, res => {
                                let resData = res;
                                resData.openid = this.data.openid
                                store.save(resData)
                                this.initOrder()
                            }, () => this.$router.push({name: 'bind', query: {from: 'order'}})
                        );
                    })
                    ;
                } else {
                    Toast.fail("无法获得授权信息，请退出重试")
                }
            }
        }
    };
</script>

